.contact-us {
  &__container {
    max-width: 1100px;
    width: 100%;
    margin: 0 auto;
    min-height: 100vh;
    padding: 150px 0 120px;
    @include r(1921) {
      padding: 150px 0 $gap-500; }
    @include r(1023) {
      padding: $gap-900 0 $gap-500; } }

  &__header-block {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: $gap-800;
    @include r(1023) {
      margin-bottom: $gap-350; }
    &--hidden {
      display: none; } }
  &__header-icon {
    margin-right: $gap-300;
    @include r(767) {
      margin-right: $gap-150; } }
  &__header-icon {
    width: 150px;
    height: 150px;
    filter: drop-shadow($shadow-1200);
    @include r(1919) {
      width: 120px;
      height: 120px; }
    @include r(1023) {
      width: 100px;
      height: 100px; }
    @include r(767) {
      width: 75px;
      height: 75px; } }
  &__header-title {
    @include rb;
    font-size: rem(62);
    @include r(1919) {
      font-size: rem(50); }
    @include r(1023) {
      font-size: rem(32); }
    @include r(767) {
      font-size: rem(28); }
    @include r(479) {
      font-size: rem(22); } }

  &__content {
    max-width: 710px;
    width: 100%;
    margin: 0 auto; }

  &__form {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    margin: 0 auto $gap-650; }

  &__success {
    display: flex;
    justify-content: flex-start;
    margin-bottom: $gap-650;
    display: none;
    &--active {
      display: block; } }

  &__label {
    width: 100%; }

  &__form-button {
    margin-top: $gap-250;
    width: 100%;
    height: 70px;
    font-size: rem(25);
    @include r(767) {
      height: 50px;
      font-size: rem(18);
      margin-top: 0; } }

  &__footer {
    display: flex;
    justify-content: space-between;
    @include r(767) {
      flex-direction: column; } }

  &__footer-location {
    max-width: 400px;
    width: 100%;
    padding-right: $gap-200;
    @include r(767) {
      margin-bottom: $gap-300; } }
  &__footer-location-country {
    font-size: rem(31);
    @include rb;
    margin-bottom: $gap-100;
    @include r(767) {
      font-size: rem(25); } }
  &__footer-location-city {
    @include rr;
    font-size: rem(20);
    @include r(767) {
      font-size: rem(18); } }

  &__footer-web {
    max-width: 180px;
    width: 100%; }
  &__footer-web-item {
    margin-bottom: $gap-50;
    font-size: rem(20);
    @include rb;
    @include r(767) {
      font-size: rem(18); }
    &:hover {
      text-decoration: underline; } } }
