.input {
  &__label {
    display: inline-block;
    margin-bottom: $gap-250;
    @include r(767) {
      margin-bottom: $gap-200; }
    &:last-child {
      margin-bottom: 0; } }
  &__input-tag {
    width: 100%;
    border: $gap-50;
    background-color: $white-100;
    font-size: rem(21);
    @include rr;
    border-radius: $gap-100;
    padding: $gap-150 $gap-200;
    outline: 0;
    color: $gray-100;
    @include r(767) {
      padding: $gap-150;
      font-size: rem(16); }
    &::placeholder {
      font-size: inherit;
      color: inherit;
      font-family: inherit; }
    &--textarea {
      height: 285px;
      resize: none;
      @include r(767) {
        height: 160px; } } } }
